.contact-banner{
     height: 50vh;
     background-color: #252831;
     justify-content: center;
     display: flex;
     align-items: center;
     border-radius:0 0% 20% 79% ;
     color: white;
     position: relative;
}
.contact-map{
     border: none;
     transition: 2s ease-in-out;
}
.contact-img{
     position: absolute;
     left: 90px;
     top:100px;
}
.contact-img img{
     width: 400px;
     height: 300px;
}
#contact{
     height: 100vh;
     display: flex;
     justify-content: center;
     align-items: center;
     font-family: 'Encode Sans', sans-serif;
}

 .form-card{
      background-color:rgb(170, 167, 167),transparent;
      opacity: 0.3px;
      padding: 10px;
      width: 40%;
      box-shadow: 2px -10px 20px black;
      border-radius: 10px;
      font-weight: 800;  

 }
 .card-title{
      margin-top: 50px;
      padding: 10px;
   
 }
 @media screen and (max-width: 600px) {
     .form-card{
          width: 90%;
     }
     .contact-img{
          position: absolute;
          left: 5px;
          top:170px;
     }
     .contact-img img{
          width: 200px;
          height: 200px;
     }
     }

     label{
          width: 30%;
          background-color:black;
          padding: 5px;
          text-align: center;
     }

.card-button{
     margin-bottom: 20px;
}