*{
     font-family: 'Encode Sans', sans-serif;
}
.banner{
     height: 50vh;
     background-image: url("https://demo.wenthemes.com/travel-gem/wp-content/themes/travel-gem/images/default-banner.jpg");
     width: 100%;
     background-size: cover;
     border-radius: 0% 0% 25% 75%;
     position: relative;
}
img.img-banner{
     width: 300px;
     margin-top: 10px;
}
.banner h1{
   
     color: white;
     font-weight: 900 !important;
     padding-top: 100px;
     text-align: center;     

}


.blog img{
     height: 400px;
     border-radius: 10px;
     position: relative;
}

.blog .card{
     height: 400px;
     border-radius: 10px;
}

@media screen and (max-width: 600px) {
     .mb-card{
          margin-bottom: 20px;
     }
    }