
#card{
     padding: 30px;
     box-shadow: 1px 7px 7px black;
     cursor: pointer;
}
#card:hover{
     padding: 30px;
     background-color: antiquewhite;
}
#blog-p{
     font-size: 20px;
}
#blog-img{
     width: 253px;
     height: 231px;
}
.card .title{
     margin-bottom: 20px;
}
div#card{
     margin-bottom: 20px;
}
.title p{
     font-size: 20px;
}

.blog-banner{
     height: 50vh;
     background-color: #252831;
     justify-content: center;
     display: flex;
     align-items: center;
     border-radius:0 0% 20% 79% ;
     color: white;
     position: relative;
     margin-bottom: 100px;
}
@media screen and (min-width: 600px) {
     
     #blog{
          position: relative;
          background-color: #252831;
          height: 200vh;
          box-shadow: 10px 10px 20px white;
          border-radius: 75% 45% 20% 75%;
          margin-bottom: 20px;
       
     }
    }